import * as React from 'react'

class Introduction extends React.Component {
  public static displayName = 'Introduction'

  public render() {
    return (
      <div className="introduction">
        <section>
          <h1>Xue-react</h1>
          <p className="text">
            Xue-react 
            <a
              href="https://xue-ui.com.cn"
              target="_blank"
              rel="noopener noreferrer"
              className="link"
            >
              Xue-ui
            </a>
             PC  React 
          </p>
        </section>
        <section>
          <h2> TypeScript</h2>
          <p className="text">
            <a
              href="https://www.tslang.cn/"
              target="_blank"
              rel="noopener noreferrer"
              className="link"
            >
              TypeScript
            </a>
            Xue-react
             TypeScript 
          </p>
        </section>
        <section>
          <h2></h2>
          <p className="text">
            Xue-react  ReactReactDOM  PropTypes
            
          </p>
        </section>
      </div>
    )
  }
}

export default Introduction



