import * as React from 'react'
import Menu from 'src/packages/menu/Menu'
import SubMenu from 'src/packages/menu/SubMenu'
import MenuItem from 'src/packages/menu/MenuItem'
import { RouteComponentProps } from 'react-router'

interface INavBarProps extends RouteComponentProps {
  xxx?: string
}

interface INavBarState {
  selectedKey: string
}

class NavBar extends React.Component<INavBarProps, INavBarState> {
  public static displayName = 'NavBar'

  public state = {
    selectedKey: 'introduction'
  }

  public componentDidMount() {
    const reg = /\/(\w+)$/
    const pathname = this.props.location.pathname
    if (reg.test(pathname)) {
      this.setState({
        selectedKey: RegExp.$1
      })
    } else {
      this.goToLink('/introduction')
    }
  }

  public componentDidUpdate(prevProps: INavBarProps, prevState: INavBarState) {
    const { selectedKey } = this.state
    if (prevState.selectedKey !== selectedKey) {
      this.goToLink(this.getPathname(selectedKey))
    }
  }

  public goToLink = (pathname: string) => {
    const { location, history } = this.props
    if (location.pathname !== pathname) {
      history.push(pathname)
    }
  }

  public handleSelectedChange = (key: string) => {
    const { selectedKey } = this.state
    if (key !== selectedKey) {
      this.setState({
        selectedKey: key
      })
    }
  }

  public getPathname = (key: string) => {
    switch (key) {
      case 'introduction':
      case 'start':
      case 'color':
      case 'zindex':
        return `/${key}`
      default:
        return `/components/${key}`
    }
  }

  public render() {
    return (
      <aside className="aside">
        <Menu
          onSelectedChange={this.handleSelectedChange}
          selectedKey={this.state.selectedKey}
          style={{ width: '275px', borderRight: 'none' }}
          defaultExpandKeys={['components']}
        >
          <MenuItem key="introduction" style={{ fontWeight: 600 }}>
            Xue-react
          </MenuItem>
          <MenuItem key="start" style={{ fontWeight: 500 }}>
            
          </MenuItem>
          <MenuItem key="color" style={{ fontWeight: 500 }}>
            
          </MenuItem>
          <MenuItem key="zindex" style={{ fontWeight: 500 }}>
            
          </MenuItem>
          <SubMenu
            key="components"
            title={<div className="nav-bar-components-title">Components</div>}
            showArrow={false}
            itemGroup={true}
          >
            <SubMenu
              key="common"
              title=""
              showArrow={false}
              itemGroup={true}
            >
              <MenuItem key="button">Button&nbsp;&nbsp;</MenuItem>
            </SubMenu>
            <SubMenu
              key="layout"
              title=""
              showArrow={false}
              itemGroup={true}
            >
              <MenuItem key="grid">Grid&nbsp;&nbsp;</MenuItem>
              {/* <MenuItem key="waterfall">Waterfall&nbsp;&nbsp;</MenuItem> */}
            </SubMenu>
            <SubMenu
              key="navigation"
              title=""
              showArrow={false}
              itemGroup={true}
            >
              <MenuItem key="affix">Affix&nbsp;&nbsp;</MenuItem>
              <MenuItem key="menu">Menu&nbsp;&nbsp;</MenuItem>
              <MenuItem key="pager">Pager&nbsp;&nbsp;</MenuItem>
            </SubMenu>
            <SubMenu
              key="dataentry"
              title=""
              showArrow={false}
              itemGroup={true}
            >
              <MenuItem key="cascader">Cascader&nbsp;&nbsp;</MenuItem>
              <MenuItem key="datepicker">
                DatePicker&nbsp;&nbsp;
              </MenuItem>
              <MenuItem key="input">Input&nbsp;&nbsp;</MenuItem>
              <MenuItem key="radio">Radio&nbsp;&nbsp;</MenuItem>
              <MenuItem key="rate">Rate&nbsp;&nbsp;</MenuItem>
              <MenuItem key="switch">Switch&nbsp;&nbsp;</MenuItem>
            </SubMenu>
            <SubMenu
              key="datadisplay"
              title=""
              showArrow={false}
              itemGroup={true}
            >
              <MenuItem key="collapse">Collapse&nbsp;&nbsp;</MenuItem>
              <MenuItem key="popover">Popover&nbsp;&nbsp;</MenuItem>
              <MenuItem key="slides">Slides&nbsp;&nbsp;</MenuItem>
              <MenuItem key="table">Table&nbsp;&nbsp;</MenuItem>
              {/* <MenuItem key="virtualtable">VirtualTable&nbsp;&nbsp;</MenuItem> */}
              <MenuItem key="tabs">Tabs&nbsp;&nbsp;</MenuItem>
            </SubMenu>
            <SubMenu
              key="feedback"
              title=""
              showArrow={false}
              itemGroup={true}
            >
              <MenuItem key="message">Message&nbsp;&nbsp;</MenuItem>
              <MenuItem key="modal">Modal&nbsp;&nbsp;</MenuItem>
              <MenuItem key="spin">Spin&nbsp;&nbsp;</MenuItem>
            </SubMenu>
            <SubMenu
              key="others"
              title=""
              showArrow={false}
              itemGroup={true}
            >
              <MenuItem key="lightup">LightUp&nbsp;&nbsp;</MenuItem>
              <MenuItem key="scroll">Scroll&nbsp;&nbsp;</MenuItem>
              <MenuItem key="transition">Transition&nbsp;&nbsp;</MenuItem>
            </SubMenu>
          </SubMenu>
        </Menu>
      </aside>
    )
  }
}

export default NavBar



