import * as React from 'react'
import * as PropTypes from 'prop-types'
import LightUp from 'src/packages/lightup/LightUp'
import Code from 'src/components/Code'
import CodeBox from 'src/components/CodeBox'
import Space from 'src/components/Space'
import { renderString } from 'src/utils/renderCode'

const componentName = 'LightUpExample'

class LightUpExample extends React.Component {
  public static displayName = componentName

  public static propTypes = {}

  public static defaultProps = {}

  public render() {
    const cn = componentName

    return (
      <div className="light-up-example">
        <section>
          <h1>LightUp </h1>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
        </section>
        <div className="example">
          <div className="container">
            <LightUp>
              <img
                src={require('../style/imgs/gakki.png')}
                alt="gakki"
                style={{ width: '200px' }}
              />
            </LightUp>
          </div>
          <CodeBox
            title=""
            description=""
          >
            {renderString(`import { LightUp } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code code={`&lt;LightUp&gt;`} />
            <Space count={4} />
            <span className="label">&lt;</span>
            <span className="htmltag">img</span>
            <span className="property"> src</span>
            <span className="htmlstr">=</span>
            <span className="htmltag">{'{'}</span>
            <span className="method">require</span>
            <span className="htmlstr">(</span>
            <span className="string">'../style/imgs/gakki.png'</span>
            <span className="htmlstr">)</span>
            <span className="htmltag">{'}'}</span>
            <span className="label"> /&gt;</span>
            <br />
            <Code code={`&lt;/LightUp&gt;`} />
          </CodeBox>
        </div>
        <section>
          <h2>API</h2>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th></th>
              <th className="des"></th>
              <th className="type"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>src</td>
              <td className="des"></td>
              <td className="type">any</td>
              <td></td>
            </tr>
            <tr>
              <td>imgClosable</td>
              <td className="des">
                
              </td>
              <td className="type">boolean</td>
              <td>false</td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default LightUpExample



