import * as React from 'react'
import Affix from 'src/packages/affix/Affix'
import Code from 'src/components/Code'
import CodeBox from 'src/components/CodeBox'
import Space from 'src/components/Space'
import { renderString } from 'src/utils/renderCode'

const componentName = 'AffixExample'

class AffixExample extends React.Component {
  public static displayName = componentName

  public render() {
    return (
      <div className="affix-example">
        <section>
          <h1>Affix </h1>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
          <p className="text">
            
          </p>
        </section>
        <section>
          <h2></h2>
        </section>
        <div className="example">
          <div className="container">
            <Affix distance={20}>
              <div className="affix-demo">Affix</div>
            </Affix>
          </div>
          <CodeBox title="" description="">
            {renderString(`import { Affix } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code code={`&lt;Affix distance={20}&gt;`} />
            <Space count={4} />
            <span className="label">&lt;</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <span className="htmlstr">Affix</span>
            <span className="label">&lt;</span>
            <span className="label">/</span>
            <span className="htmltag">div</span>
            <span className="label">&gt;</span>
            <br />
            <Code code={`&lt;/Affix&gt;`} />
          </CodeBox>
        </div>
        <section>
          <h2>API</h2>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th></th>
              <th className="des"></th>
              <th className="type"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>distance</td>
              <td className="des"></td>
              <td className="type">number</td>
              <td>0</td>
            </tr>
            <tr>
              <td>className</td>
              <td className="des"></td>
              <td className="type">string</td>
              <td></td>
            </tr>
            <tr>
              <td>style</td>
              <td className="des"></td>
              <td className="type">React.CSSProperties</td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default AffixExample



