import * as React from 'react'
import Button from 'src/packages/button/Button'
import ButtonGroup from 'src/packages/button/ButtonGroup'
import Code from 'src/components/Code'
import CodeBox from 'src/components/CodeBox'
import Space from 'src/components/Space'
import { renderString } from 'src/utils/renderCode'

interface IButtonExampleState {
  size: 'small' | 'medium' | 'large'
  loading: boolean
}

class ButtonExample extends React.Component<{}, IButtonExampleState> {
  public static displayName = 'ButtonExample'

  public state: IButtonExampleState = {
    size: 'medium',
    loading: false
  }

  public handleChangeSize = (size: 'small' | 'medium' | 'large') => {
    this.setState({ size })
  }

  public handleClick = () => {
    this.setState({
      loading: !this.state.loading
    })
  }

  public render() {
    const { loading } = this.state
    return (
      <div className="button-example">
        <section>
          <h1>Button </h1>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
          <p className="text"></p>
        </section>
        <section>
          <h2></h2>
        </section>
        <div className="example">
          <div className="container">
            <Button style={{ marginRight: '20px' }}>Default</Button>
            <Button style={{ marginRight: '20px' }} type="primary">
              Primary
            </Button>
            <Button style={{ marginRight: '20px' }} type="dashed">
              Dashed
            </Button>
            <Button style={{ marginRight: '20px' }} type="danger">
              Danger
            </Button>
          </div>
          <CodeBox
            title=""
            description=""
          >
            {renderString(`import { Button } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code code={`&lt;Button&gt;Default&lt;/Button&gt;`} />
            <Code
              code={`&lt;Button type="primary"&gt;Primary&lt;/Button&gt;`}
            />
            <Code code={`&lt;Button type="dashed"&gt;Dashed&lt;/Button&gt;`} />
            <Code code={`&lt;Button type="danger"&gt;Danger&lt;/Button&gt;`} />
          </CodeBox>
        </div>
        <div className="example">
          <div className="container">
            <Button disabled={true} style={{ marginRight: '20px' }}>
              Disabled
            </Button>
            <Button
              loading={true}
              type="primary"
              style={{ marginRight: '20px' }}
            >
              Loading
            </Button>
            <Button
              icon="setting"
              loading={loading}
              type="primary"
              onClick={this.handleClick}
            >
              Click me
            </Button>
          </div>
          <CodeBox
            title="Disabled  Loading "
            description=""
          >
            {renderString(`import { Button } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code
              code={`&lt;Button disabled={true}&gt;Disabled&lt;/Button&gt;`}
            />
            <Code
              code={`&lt;Button loading={true} type="primary"&gt;Loading&lt;/Button&gt;`}
            />
            <Code
              code={`&lt;Button icon="setting" loading={this.state.loading} type="primary" onClick={this.handleClick}&gt;Click me&lt;/Button&gt;`}
            />
          </CodeBox>
        </div>
        <div className="example">
          <div className="container">
            <Button style={{ marginRight: '20px' }} icon="setting">
              Icon
            </Button>
            <Button icon="setting" position="right">
              Icon
            </Button>
          </div>
          <CodeBox title="Icon " description=" Icon ">
            {renderString(`import { Button } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code code={`&lt;Button icon="setting"&gt;Icon&lt;/Button&gt;`} />
            <Code
              code={`&lt;Button icon="setting" position="right"&gt;Icon&lt;/Button&gt;`}
            />
          </CodeBox>
        </div>
        <div className="example">
          <div
            className="container"
            style={{ backgroundColor: 'rgb(190,200,200)' }}
          >
            <Button style={{ marginRight: '20px' }} ghost={true}>
              Default
            </Button>
            <Button style={{ marginRight: '20px' }} ghost={true} type="primary">
              Primary
            </Button>
            <Button style={{ marginRight: '20px' }} ghost={true} type="dashed">
              Dashed
            </Button>
            <Button ghost={true} type="danger">
              Danger
            </Button>
          </div>
          <CodeBox title="" description="">
            {renderString(`import { Button } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code code={`&lt;Button ghost={true}&gt;Default&lt;/Button&gt;`} />
            <Code
              code={`&lt;Button ghost={true} type="primary"&gt;Primary&lt;/Button&gt;`}
            />
            <Code
              code={`&lt;Button ghost={true} type="dashed"&gt;Dashed&lt;/Button&gt;`}
            />
            <Code
              code={`&lt;Button ghost={true} type="danger"&gt;Danger&lt;/Button&gt;`}
            />
          </CodeBox>
        </div>
        <div className="example">
          <div className="container">
            <Button
              style={{ marginRight: '20px' }}
              size={this.state.size}
              onClick={() => this.handleChangeSize('small')}
              type="primary"
            >
              Small
            </Button>
            <Button
              style={{ marginRight: '20px' }}
              size={this.state.size}
              onClick={() => this.handleChangeSize('medium')}
              type="primary"
            >
              Medium
            </Button>
            <Button
              size={this.state.size}
              onClick={() => this.handleChangeSize('large')}
              type="primary"
            >
              Large
            </Button>
          </div>
          <CodeBox
            title=""
            description=""
          >
            {renderString(`import { Button } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code
              code={`&lt;Button size={this.state.size} onClick={() => this.handleChangeSize('small')} type="primary"&gt;Small&lt;/Button&gt;`}
            />
            <Code
              code={`&lt;Button size={this.state.size} onClick={() => this.handleChangeSize('medium')} type="primary"&gt;Medium&lt;/Button&gt;`}
            />
            <Code
              code={`&lt;Button size={this.state.size} onClick={() => this.handleChangeSize('large')} type="primary"&gt;Large&lt;/Button&gt;`}
            />
          </CodeBox>
        </div>
        <div className="example">
          <div className="container">
            <ButtonGroup>
              <Button>Left</Button>
              <Button>Middle</Button>
              <Button>Right</Button>
            </ButtonGroup>
          </div>
          <CodeBox title="" description="">
            {renderString(`import { Button } from 'xue-react'`)}
            {renderString(`import { ButtonGroup } from 'xue-react'`)}
            {renderString(`import 'xue-react/lib/xue-react.css'`)}
            <br />
            <Code code={`&lt;ButtonGroup&gt;`} />
            <Space count={4} />
            <Code code={`&lt;Button&gt;Left&lt;/Button&gt;`} />
            <Space count={4} />
            <Code code={`&lt;Button&gt;Middle&lt;/Button&gt;`} />
            <Space count={4} />
            <Code code={`&lt;Button&gt;Right&lt;/Button&gt;`} />
            <Code code={`&lt;/ButtonGroup&gt;`} />
          </CodeBox>
        </div>
        <section>
          <h2>API</h2>
        </section>
        <table className="api-table">
          <thead>
            <tr>
              <th></th>
              <th className="des"></th>
              <th className="type"></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td>icon</td>
              <td className="des"> Icon  name </td>
              <td className="type">string</td>
              <td></td>
            </tr>
            <tr>
              <td>position</td>
              <td className="des"> Icon </td>
              <td className="type">'left' | 'right'</td>
              <td>'left'</td>
            </tr>
            <tr>
              <td>type</td>
              <td className="des"></td>
              <td className="type">
                'default' | 'dashed' | 'primary' | 'danger'
              </td>
              <td>'default'</td>
            </tr>
            <tr>
              <td>size</td>
              <td className="des"></td>
              <td className="type">'small' | 'medium' | 'large'</td>
              <td>'medium'</td>
            </tr>
            <tr>
              <td>loading</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td>false</td>
            </tr>
            <tr>
              <td>disabled</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td>false</td>
            </tr>
            <tr>
              <td>ghost</td>
              <td className="des"></td>
              <td className="type">boolean</td>
              <td>false</td>
            </tr>
            <tr>
              <td>htmlType</td>
              <td className="des"> type </td>
              <td className="type">'button' | 'submit' | 'reset'</td>
              <td>'button'</td>
            </tr>
            <tr>
              <td>onClick</td>
              <td className="des"></td>
              <td className="type">React.MouseEventHandler</td>
              <td></td>
            </tr>
            <tr>
              <td>className</td>
              <td className="des"> Button </td>
              <td className="type">string</td>
              <td></td>
            </tr>
            <tr>
              <td>style</td>
              <td className="des"> Button </td>
              <td className="type">React.CSSProperties</td>
              <td></td>
            </tr>
          </tbody>
        </table>
      </div>
    )
  }
}

export default ButtonExample



